import * as React from "react";
import JSZip = require("jszip");
import JSZipUtils = require("jszip-utils");
import Docxtemplater = require("docxtemplater");
import {saveAs} from "browser-filesaver";
import * as ReactDOM from "react-dom";
import {PureComponent, StatelessComponent} from "react";


import {render} from "react-dom";
import {InputFields} from "./tag-fields/input-fields";
import {categoryIncassokosten} from "./tag-fields/incassokosten";

function isElement(x: Node): x is HTMLInputElement {
    return !!x && x.nodeType === Node.ELEMENT_NODE && x.nodeName.toUpperCase() === "INPUT";
}

function isFileUpload(x: Node): x is HTMLInputElement {
    return !!x && x.nodeType === Node.ELEMENT_NODE && x.nodeName.toUpperCase() === "INPUT";
}

class InspectModule {
    public inspect: any;

    constructor() {
        this.inspect = {};
    }

    set(obj: InspectModule) {
        if (obj.inspect) {
            this.inspect = Object.assign({}, this.inspect, obj.inspect);
        }
    }
}

export type OnChangeField = (k: string, v: any, appliedCategory?: string) => any;

const getTags = function (postParsed: any) {
    return postParsed.filter(function (part: any) {
        return part.type === "placeholder";
    }).reduce(function (tags: any, part: any) {
        tags[part.value] = {};
        if (part.subparsed) {
            tags[part.value] = getTags(part.subparsed);
        }
        return tags;
    }, {});
};


function loadFile(url: string, callback: any) {
    JSZipUtils.getBinaryContent(url, callback);
}

export interface AppProps {
}

export interface AppState {
    file?: any;
    tags: any;
    appliedCostsCategory: string;
}

export class App extends PureComponent<AppProps, AppState> {
    constructor(props: AppProps) {
        super(props);
        this.state = {
            file: undefined,
            tags: {},
            appliedCostsCategory: categoryIncassokosten.UNDER_2500.title
        };
    }

    onFileChange(e: any) {
        const app = this;
        const files = (e.target as any).files;
        console.log(files);
        const file = files[0];
        const fr = new FileReader();
        fr.readAsArrayBuffer(file);
        // Closure to capture the file information.
        fr.onload = (function (file) {
            return function (e: any) {
                const zip = new JSZip(e.target.result);
                const doc = new Docxtemplater().loadZip(zip);

                let inspectModule = new InspectModule();
                doc.attachModule(inspectModule);


                try {
                    doc.compile();
                }
                catch (error) {
                    const e = {
                        message: error.message,
                        name: error.name,
                        stack: error.stack,
                        properties: error.properties,
                    };
                    console.log(JSON.stringify({error: e}));
                    // The error thrown here contains additional information when logged with JSON.stringify (it contains a property object).
                    throw error;
                }
                let postParsed = inspectModule.inspect.postparsed;
                const tags = getTags(postParsed);

                app.setState({
                    file,
                    tags
                });
            };
        })(file);
    }

    exportFile() {
        const reader = new FileReader();
        reader.readAsArrayBuffer(this.state.file);
        // Closure to capture the file information.
        const tags = this.state.tags;
        reader.onload = (function (e: any) {
            const zip = new JSZip(e.target.result);
            const doc = new Docxtemplater().loadZip(zip);

            //console.log(items);
            const items: any = {};
            Object.keys(tags).forEach(k => {
                    const tagValue = tags[k];
                    items[k] = tagValue && typeof tagValue === "string"
                        ? tagValue
                        : tagValue.value
                            ? tagValue.value
                            : "";
                }
            );
            doc.setData(items);
            try {
                doc.render();
            }
            catch (error) {
                const e = {
                    message: error.message,
                    name: error.name,
                    stack: error.stack,
                    properties: error.properties,
                };
                console.log(JSON.stringify({error: e}));
                // The error thrown here contains additional information when logged with JSON.stringify (it contains a property object).
                throw error;
            }

            const out = doc.getZip().generate({
                type: "blob",
                mimeType: "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
            });
            // window.open(uriContent, 'neuesDokument');
            saveAs(out, "output.docx");
        });
    }

    onChangeTagField(k: string, v: any, appliedCategory?: string) {
        console.log(`Field changed: [${k}] -> ${v}`);
        const tags = this.state.tags;

        let newState = {tags: Object.assign({}, tags, {[k]: v})};
        if (appliedCategory) {
            this.setState(Object.assign(newState, {appliedCostsCategory: appliedCategory}));
        } else {
            this.setState(newState);
        }
    }

    render() {
        return <div>
            <input onChange={(e) => this.onFileChange(e)} type="file"
                   id="my-file"/>
            <InputFields
                {...this.state}
                onChangeListener={(k: string, v: string, appliedCategory?: string) => this.onChangeTagField(k, v, appliedCategory)}
                tags={this.state.tags}/>
            {this.state.tags && this.state.file ? <div className="align-center">
                <button onClick={() => this.exportFile()} id="btn-export">
                    Exporteer
                </button>
            </div> : ""}
        </div>;
    }
}

const mountNode = document.getElementById("mount-node");
if (!!mountNode) {
    ReactDOM.render(
        <App/>,
        mountNode
    );
}


