import * as React from "react";
import {ChangeEvent, StatelessComponent} from "react";
import {
    InputField, InputFieldProps,
    setValueForTagUncontrolled
} from "./input-field";

export interface WettelijkeRenteProps extends InputFieldProps {
    tags: any;
}

export const WettelijkeRente: StatelessComponent<WettelijkeRenteProps> = (props) => {
    const {tag, tags, i, onChangeListener} = props;

    function onChange(isHandelspartij: boolean, i: number | string) {
        return (e: ChangeEvent<HTMLInputElement>) => {
            if ((e.target as any).checked as boolean) {
                const value = isHandelspartij ? "wettelijke handelsrente" : "wettelijke rente";
                onChangeListener(tag, {
                    value,
                    isHandelspartij
                });

                setValueForTagUncontrolled(i, value);
            }
        };
    }

    return <div key={i + "-wrapper"} className="table-row-group">
        <div className="tr template-tag">
            <div className="td"/>
            <div className="td">
                <div>
                    <div className="mdc-form-field">
                        <div className="mdc-radio">
                            <input className="mdc-radio__native-control"
                                   type="radio"
                                   id="radio-handelspartij"
                                   onChange={onChange(true, i)}
                                   name="ex1-custom"/>
                            <div className="mdc-radio__background">
                                <div className="mdc-radio__outer-circle"/>
                                <div className="mdc-radio__inner-circle"/>
                            </div>
                        </div>
                        <label id="radio-handelspartij-label"
                               htmlFor="radio-handelspartij">Handelspartij</label>
                    </div>
                </div>
                <div>
                    <div className="mdc-form-field">
                        <div className="mdc-radio demo-radio--custom"
                             data-demo-no-js="">
                            <input
                                className="mdc-radio__native-control"
                                onChange={onChange(false, i)}
                                type="radio"
                                id="radio-geen-handelspartij"
                                name="ex1-custom"/>
                            <div className="mdc-radio__background">
                                <div className="mdc-radio__outer-circle"/>
                                <div className="mdc-radio__inner-circle"/>
                            </div>
                        </div>
                        <label id="radio-geen-handelspartij-label"
                               htmlFor="radio-geen-handelspartij">Geen
                            handelspartij</label>
                    </div>
                </div>
            </div>
        </div>
        <InputField {...props}
                    extraClassNames={[""]}/>
    </div>;
    // val={tags[tag].value}
    // inactive={true}
};


