import * as React from "react";
import {HTMLProps, StatelessComponent} from "react";
import {OnChangeField} from "../app";

export interface InputFieldProps {
    tag: string;
    i: string;
    onChangeListener: OnChangeField;
    extraClassNames?: string[];
    inactive?: boolean;
    val?: string;
}

export function setValueForTagUncontrolled(i: number | string, value: string) {
    const resultId = `tag-${i}`;
    const resultInput = document.getElementById(resultId) as HTMLInputElement;
    if (resultInput) {
        resultInput.value = value;
    }
}

export const InputField: StatelessComponent<InputFieldProps> = ({
                                                                    tag,
                                                                    i,
                                                                    onChangeListener,
                                                                    extraClassNames,
                                                                    inactive,
                                                                    val
                                                                }) => {
        const classNames = ((!!extraClassNames) ? extraClassNames : []).concat(["tr template-tag mdc-form-field"]);
        const inputProps: HTMLProps<HTMLInputElement> = {
            key: "input-" + i,
            onChange: (e: any) => onChangeListener(tag, e.target.value),
            disabled: inactive,
            type: "text",
            className: "mdc-text-field__input",
            id: `tag-${i}`,
            placeholder: tag
        };
        if (typeof val === "string") inputProps.value = val;

        return <div className={classNames.join(" ")}>
            <label className="td prop-label" htmlFor={`tag-${i}`}>{tag}</label>
            <div className="td mdc-text-field">
                {React.createElement("input", inputProps)}
                <div className="mdc-text-field__bottom-line"/>
            </div>
        </div>;
    }
;


